import { Component, OnInit } from '@angular/core';
import { Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-add-studend',
  templateUrl: './add-studend.component.html',
  styleUrls: ['./add-studend.component.css']
})
export class AddStudendComponent implements OnInit {

  newStudent:any;

  @Output() emitStudent: EventEmitter<any> = new EventEmitter();
  constructor() {
    this.newStudent = {
      firstName:'',
      lastName:'',
      cardNumber:''
    }
   }

  ngOnInit() {
  }

  save(){
    this.emitStudent.next(this.newStudent);
  }

}



