import { Component } from '@angular/core';
import { Record, AuthorInterface } from './record.model';
import { Http, Response, RequestOptions, 
         Headers, URLSearchParams } from '@angular/http';

import * as _ from 'lodash';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})
export class AppComponent {
  students: any[];

  constructor(private http: Http){
   http.get('api/students').subscribe(
     (res:Response)=>{
       this.students = res.json();
     }
   ); 
  }

  save(student:any){
    console.log(student);
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    this.http.post('/api/students',JSON.stringify(student),options).subscribe(
      (res: Response) => {
        this.http.get('api/students').subscribe(
          (res:Response)=>{
            this.students = res.json();
          }
        ); 
      }
    );
  }
}



